import { Component, Prop, State, Event, EventEmitter, h, AttachInternals } from '@stencil/core';
import { syncFormValue, updateValidity } from '../../utils/form';

@Component({
  tag: 'bh-input-text',
  styleUrl: 'bh-input-text.css',
  shadow: true,
  formAssociated: true,
})
export class BhInputText {
  @AttachInternals() internals!: ElementInternals;

  private inputEl?: HTMLInputElement;
  /**
   * The label for the input
   */
  @Prop() label: string = 'Email';

  /**
   * Placeholder text
   */
  @Prop() placeholder: string = 'fujiwara@act-hq.com';

  /**
   * Hint text shown below the input
   */
  @Prop() hintText: string = '';

  /**
   * Whether to show the label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Whether to show hint text
   */
  @Prop() showHintText: boolean = true;

  /**
   * Whether to show the help icon
   */
  @Prop() showHelpIcon: boolean = false;

  /**
   * Whether to show a leading icon
   */
  @Prop() showLeadingIcon: boolean = false;

  /**
   * Whether to show a trailing icon
   */
  @Prop() showTrailingIcon: boolean = false;

  /**
   * Whether the field has an error
   */
  @Prop() error: boolean = false;

  /**
   * Whether the input is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether the field is required
   */
  @Prop() required: boolean = false;

  /**
   * Tooltip text for the help icon
   */
  @Prop() helpIconTooltip: string = 'Help';

  /**
   * Leading icon name (Material Symbols)
   */
  @Prop() leadingIcon: string = 'mail';

  /**
   * Trailing icon name (Material Symbols)
   */
  @Prop() trailingIcon: string = 'search';

  /**
   * The input value
   */
  @Prop({ mutable: true }) value: string = '';

  /**
   * Name attribute for form submission
   */
  @Prop() name: string = '';

  /**
   * Default value for form reset
   */
  @Prop() defaultValue: string = '';

  /**
   * Custom validation message
   */
  @Prop() validationMessage: string = '';

  /**
   * Input type (text, email, tel, url, etc.)
   */
  @Prop() type: string = 'text';

  /**
   * Width of the input (e.g., '100%', '200px')
   */
  @Prop() width: string = '';

  /**
   * Whether the input is read-only
   */
  @Prop() readOnly: boolean = false;

  /**
   * Visual state of the input (for programmatic control)
   */
  @Prop() state: 'default' | 'error' | 'disabled' | 'focus' | 'filled' | 'hover' = 'default';

  /**
   * Track focus state
   */
  @State() isFocused: boolean = false;

  /**
   * Track hover state
   */
  @State() isHovered: boolean = false;

  /**
   * Emitted when the value changes
   */
  @Event() bhInput!: EventEmitter<string>;

  /**
   * Emitted when the input gains focus
   */
  @Event() bhFocus!: EventEmitter<void>;

  /**
   * Emitted when the input loses focus
   */
  @Event() bhBlur!: EventEmitter<void>;

  /**
   * Emitted when the help icon is clicked
   */
  @Event() bhHelpClick!: EventEmitter<void>;

  private handleInput = (event: Event): void => {
    const target = event.target as HTMLInputElement;
    this.value = target.value;
    this.bhInput.emit(this.value);
    syncFormValue(this);
    updateValidity(this.internals, this.inputEl, this.validationMessage);
  };

  componentDidLoad() {
    syncFormValue(this);
    updateValidity(this.internals, this.inputEl, this.validationMessage);
  }

  formResetCallback() {
    this.value = this.defaultValue;
    if (this.inputEl) {
      this.inputEl.value = this.defaultValue;
    }
    syncFormValue(this);
    updateValidity(this.internals, this.inputEl);
  }

  private handleFocus = (): void => {
    this.isFocused = true;
    this.bhFocus.emit();
  };

  private handleBlur = (): void => {
    this.isFocused = false;
    this.bhBlur.emit();
  };

  private handleMouseEnter = (): void => {
    if (!this.isFocused) {
      this.isHovered = true;
    }
  };

  private handleMouseLeave = (): void => {
    this.isHovered = false;
  };

  private handleHelpClick = (): void => {
    this.bhHelpClick.emit();
  };

  render() {
    const containerClasses = {
      'input-container': true,
      'input-container-focused': this.isFocused,
      'input-container-hover': this.isHovered && !this.isFocused,
      'input-container-error': this.error,
      'input-container-disabled': this.disabled,
      'input-container-with-leading': this.showLeadingIcon,
    };

    const wrapperStyle = this.width ? { width: this.width } : {};

    return (
      <div class="input-wrapper" part="wrapper" style={wrapperStyle}>
        <bh-label
          label={this.showLabel ? this.label : ''}
          required={this.required}
          showHelpIcon={this.showHelpIcon}
          helpIconTooltip={this.helpIconTooltip}
          disabled={this.disabled}
          onBhHelpClick={this.handleHelpClick}
        >
          <div
            class={containerClasses}
            onMouseEnter={this.handleMouseEnter}
            onMouseLeave={this.handleMouseLeave}
          >
            <div class="input-content">
              {this.showLeadingIcon && (
                <span class="material-symbols-outlined leading-icon">
                  {this.leadingIcon}
                </span>
              )}
              <input
                ref={(el) => (this.inputEl = el)}
                type={this.type}
                class="input-element"
                name={this.name}
                value={this.value}
                placeholder={this.placeholder}
                disabled={this.disabled}
                readOnly={this.readOnly}
                required={this.required}
                aria-label={this.label}
                aria-describedby={
                  this.showHintText && this.hintText ? 'hint-text' : undefined
                }
                aria-invalid={String(this.error)}
                onInput={this.handleInput}
                onFocus={this.handleFocus}
                onBlur={this.handleBlur}
                part="input"
              />
              {this.showTrailingIcon && (
                <span class="material-symbols-outlined trailing-icon">
                  {this.trailingIcon}
                </span>
              )}
            </div>
          </div>
        </bh-label>

        {this.showHintText && this.hintText && (
          <div
            class={{ 'hint-text': true, 'hint-error': this.error }}
            id="hint-text"
          >
            {this.hintText}
          </div>
        )}
      </div>
    );
  }
}
